.headerZag{
  color: white;
  margin-top: 0;
  padding-top: 20px;
  font-size: 40px;
}
.headerCont{
  height: 300px;
  background-color: rgb(29, 26, 26);
  text-align: center;
}
.btnSort{
  width: 80px;
  height: 40px;
  color:rgb(24, 26, 26);
  background-color: white;
}
.btnActive{
  width: 80px;
  height: 40px;
  border: none;
  background-color:rgb(81, 82, 82);
  color: white;
}
.filters{
  display: flex;
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  font-size: 20px;
  text-align: center;
}
.filtItem{
  color :white;
  margin-left: 5px;
  margin-right: 5px;
  margin-top: 0;
  margin-bottom: 0;
}
.activeItem {
  color:rgb(81, 82, 82);
  margin-left: 5px;
  margin-right: 5px;
  margin-top: 0;
  margin-bottom: 0;
}
.inputSearch{
  height: 30px;
}
.rowFilts{
  display: flex;
  margin-top: 10px;
  margin-bottom: 10px;
}
.btnSearch{
  height: 35px;
  width: 60px;
  background-color: rgb(81, 82, 82);
  color : white;
  border-radius: 3px;
}
.filtsCent{
  display: flex;
  margin-right: auto;
  margin-left: auto;
}


@media(max-width: 600px){
  .filtItem{
    font-size: medium;
  }
  .activeItem {
    font-size: medium;
  }
}
@media(max-width: 500px) {
  .filtsCent{
    display: block;
  }
  .rowFilts{
    margin-top: 5px;
    margin-bottom: 5px;
  }
}